.container {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: auto;
  padding: 2rem;
  background: #f0f0f0;
  border-radius: 1rem;


input {
  margin-bottom: 1rem;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 0.5rem;
}

button {
  background: #198754;
  color: white;
  border: none;
  padding: 0.75rem;
  border-radius: 0.5rem;
  cursor: pointer;

  &:hover {
    background: #157347;
  }
}
}